import { pgrestApi as api } from "../../config/axiosInstances";
import types from "../mutation-types";

const state = {
  id: null,
  provider_id: null
};

const mutations = {
  [types.PROVIDER_CRAWLER_GET](state, payload) {
    for (const key in payload) {
      if (payload.hasOwnProperty(key)) {
        state[key] = payload[key];
      }
    }
  }
};

const actions = {
  [types.PROVIDER_CRAWLER_LIST]() {
    return new Promise((resolve, reject) => {
      api
        .get("/provider_crawlers", {
          headers: { Prefer: "return=representation" }
        })
        .then(
          response => {
            resolve(response);
          },
          error => {
            reject(error);
          }
        );
    });
  },
  [types.PROVIDER_CRAWLER_CREATE]() {
    return new Promise((resolve, reject) => {
      api
        .post(
          "/provider_crawlers",
          {},
          {
            headers: { Prefer: "return=representation" }
          }
        )
        .then(
          response => {
            resolve(response.data[0]);
          },
          error => {
            reject(error);
          }
        );
    });
  },
  [types.PROVIDER_CRAWLER_GET]({ commit }, { id }) {
    return new Promise((resolve, reject) => {
      api
        .get(`/provider_crawlers?id=eq.${id}`, {
          headers: { Prefer: "return=representation" }
        })
        .then(
          response => {
            resolve(response.data[0]);
          },
          error => {
            reject(error);
          }
        );
    });
  },
  // eslint-disable-next-line no-empty-pattern
  [types.PROVIDER_CRAWLER_CHANGE_SITEMAP]({}, { id, sitemapId, sitemapUrl }) {
    return new Promise((resolve, reject) => {
      api
        .patch(
          `/provider_crawlers?id=eq.${id}`,
          {
            sitemaps: [{ id: sitemapId, url: sitemapUrl }]
          },
          {
            headers: { Prefer: "return=representation" }
          }
        )
        .then(
          response => {
            resolve(response.data[0]);
          },
          error => {
            reject(error);
          }
        );
    });
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};
