<template>
  <div class="el:amx-Bot">
    <tabs
      nav-class="el:amx-Bc_su"
      content-class="el:amx-Bc_su"
      :size="3"
      rootTabName="domain-edit-tab"
      :current-tab="currentTab"
      :active-tab-mutator="currentTab"
      :nav-containers="['container', 'row', 'col']"
      :content-containers="['test']"
      @onTabClick="changeTab"
    >
      <template #domain-edit-tab1>
        Status
      </template>

      <template #domain-edit-tab2>
        Sitemap
      </template>

      <template #domain-edit-tab3>
        Debug Tool
      </template>

      <template #domain-edit-tab1-content>
        <router-view v-if="$route.name == 'domain-status'"></router-view>
      </template>

      <template #domain-edit-tab2-content>
        <router-view v-if="$route.name == 'domain-sitemap'"></router-view>
      </template>

      <template #domain-edit-tab3-content>
        <router-view v-if="$route.name == 'domain-debugtool'"></router-view>
      </template>
    </tabs>
  </div>
</template>
<script>
/* eslint-disable no-undef */
export default {
  data() {
    return {
      tabs: {
        "domain-edit-tab1": "domain-status",
        "domain-edit-tab2": "domain-sitemap",
        "domain-edit-tab3": "domain-debugtool"
      }
    };
  },
  computed: {
    currentTab() {
      // eslint-disable-next-line no-undef
      return this.getTab(this.$route.name);
    }
  },
  methods: {
    getTab(name) {
      return _.invert(this.tabs)[name];
    },
    changeTab(tab) {
      this.$router.push({
        name: this.tabs[tab]
      });
    }
  }
};
</script>
