import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

// Import Session
import session from "../config/session";

// Components
import Home from "../Home.vue";
import DomainNew from "../components/DomainNew";
import DomainEdit from "../components/DomainEdit";
import DomainConfig from "../components/DomainConfig";
import DomainSitemap from "../components/DomainSitemap";
import DomainDebugTool from "../components/DomainDebugTool";

const routes = [
  {
    path: "/dashboard",
    name: "root",
    component: Home,
    meta: { requiresAuth: true },
    children: [
      {
        path: "domains/new",
        name: "domain-new",
        component: DomainNew
      },
      {
        path: "domains/:domain",
        name: "domain-edit",
        redirect: "domains/:domain/status",
        component: DomainEdit,
        children: [
          {
            path: "status",
            name: "domain-status",
            component: DomainConfig,
            meta: {
              parent: "domain"
            }
          },
          {
            path: "sitemap",
            name: "domain-sitemap",
            component: DomainSitemap,
            meta: {
              parent: "domain"
            }
          },
          {
            path: "debug-tool",
            name: "domain-debugtool",
            component: DomainDebugTool,
            meta: {
              parent: "domain"
            }
          }
        ]
      }
    ]
  }
];

const router = new VueRouter({
  mode: "history",
  routes
});

// Authenticate and request a JWT
router.beforeEach((to, _from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    const redirPath = to.query.developers_dashboard_redir;

    if (session.isLoggedIn()) {
      next(redirPath);
    } else {
      next(false);
      session.requireSignIn(to.path);
    }
  } else {
    next();
  }
});

export default router;
