<template>
  <div class="el:amx-Pos(r)">
    <div class="el:amx-Pos(a)" style="width:100%;">
      <progress-bar v-if="loading" :indeterminate="true" />
    </div>
    <validation-observer v-slot="{ invalid, validate }" tag="div">
      <div class="container">
        <div class="row" v-if="!loading">
          <div class="col-12">
            <div class="el:amx-Pt(1em) el:amx-Pb(1em)">
              <validation-provider
                #default="{ errors }"
                tag="div"
                name="sitemap_url"
                :rules="`required|url_path:${domain.domain}`"
              >
                <div
                  class="el:m-form-field el:amx-Mb(1em)"
                  :class="[
                    (errors.length > 0 || sitemap.error) &&
                      'el:m-form-field--error',
                    submitting && 'el:m-form-field--disabled'
                  ]"
                >
                  <div
                    class="el:m-form-field__label el:m-form-field__label--over"
                  >
                    <label for="field">
                      Sitemap
                    </label>
                  </div>

                  <div
                    style="min-width:320px"
                    class="el:m-form-field__input el:m-form-field__input--medium el:amx-Mb(0.25em)"
                  >
                    <input
                      name="field"
                      style="width:450px"
                      type="text"
                      v-model.trim="sitemap.value"
                      @input="resetSitemapCheck"
                      :placeholder="
                        `e.g. https://${domain.domain}/path/to/sitemap.xml`
                      "
                    />
                    <icon
                      width="1.125em"
                      height="1.125em"
                      name="wrong"
                      class="el:m-form-field__input-icon el:amx-C_er"
                      v-if="errors.length > 0 || sitemap.error"
                    ></icon>
                    <icon
                      width="1.125em"
                      height="1.125em"
                      name="checked"
                      class="el:m-form-field__input-icon el:amx-C_pr"
                      v-if="
                        sitemap.value && errors.length == 0 && !sitemap.error
                      "
                    ></icon>
                  </div>
                  <button
                    type="submit"
                    class="btn btn--primary-border btn--sm el:amx-Ml(1em)"
                    :disabled="submitting || invalid || sitemap.validating"
                    :value="submitting ? 'Changing...' : 'Change'"
                    @click.prevent="changeSitemap"
                  >
                    {{ submitting ? "Changing..." : "Change" }}
                  </button>
                  <div
                    v-if="errors.length > 0 || sitemap.error"
                    class="el:amx-Mb(0.5em) el:amx-C_er"
                  >
                    <span class="el:amx-Fs(0.75em)">{{
                      errors[0] || sitemap.error
                    }}</span>
                  </div>

                  <div class="el:amx-Fs(0.75em)">
                    <table
                      style="margin-left:-0.5em"
                      cellspacing="0"
                      cellpadding="0"
                    >
                      <tr>
                        <td class="el:amx-Fw(b)">Status</td>
                        <td>{{ sitemap.status }}</td>
                      </tr>
                      <tr>
                        <td class="el:amx-Fw(b)">Type</td>
                        <td>{{ sitemap.type }}</td>
                      </tr>
                    </table>
                  </div>
                </div>
              </validation-provider>
            </div>
          </div>
        </div>
      </div>
      <div v-if="!loading">
        <div class="container">
          <div class="row">
            <div class="col-12">
              <h4 class="el:amx-Mb(0.5em)">
                System Log
              </h4>
              <log-output :log="sitemap.log"></log-output>
            </div>
          </div>
        </div>
      </div>
    </validation-observer>
  </div>
</template>
<script>
import normalizeUrl from "normalize-url";
import ProgressBar from "external/ProgressBar.vue";
import Icon from "external/Icon.vue";
import LogOutput from "./LogOutput.vue";
import { createNamespacedHelpers } from "vuex";
import types from "../store/mutation-types";

const { mapActions: mapCrawlerDomainActions } = createNamespacedHelpers(
  "crawlerDomain"
);

const { mapActions: mapProviderCrawlerActions } = createNamespacedHelpers(
  "providerCrawler"
);

export default {
  data() {
    return {
      domain: null,
      providerCrawler: null,
      submitting: false,
      loading: false,
      sitemap: {
        id: null,
        validating: false,
        log: [],
        value: null,
        status: "-",
        type: "unknown",
        interval: null,
        error: null
      }
    };
  },
  components: {
    ProgressBar,
    LogOutput,
    Icon
  },
  created() {
    this.fetchData();
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    ...mapCrawlerDomainActions({
      fetchCrawlerDomain: types.CRAWLER_DOMAIN_GET
    }),
    ...mapProviderCrawlerActions({
      fetchProviderCrawler: types.PROVIDER_CRAWLER_GET,
      changeProviderCrawlerSitemap: types.PROVIDER_CRAWLER_CHANGE_SITEMAP
    }),
    fetchData() {
      this.domain = null;
      this.loading = true;

      this.fetchCrawlerDomain({
        domain: this.$route.params.domain
      }).then(
        domain => {
          this.loading = false;
          this.domain = domain;
          this.providerCrawler = domain.provider_crawler;
          this.loadSitemap(this.providerCrawler);
        },
        error => {
          if (error.response && error.response.data) {
            this.error = error.response.data.message;
          }
          console.log(error);
        }
      );
    },
    changeSitemap() {
      this.submitting = true;
      this.sitemap.id = this.$uuid();
      this.sitemap.validating = true;
      this.sitemap.log = [];
      this.sitemap.interval = null;
      this.sitemap.error = null;

      // PROVIDER_CRAWLER_CHANGE_SITEMAP
      this.changeProviderCrawlerSitemap({
        id: this.providerCrawler.id,
        sitemapId: this.sitemap.id,
        sitemapUrl: this.sitemap.value
      }).then(
        providerCrawler => {
          this.loadSitemap(providerCrawler);
          this.pollOnSitemapStatus();
        },
        error => {
          console.log(error);
          this.submitting = false;
        }
      );
    },
    loadSitemap(providerCrawler) {
      if (providerCrawler && providerCrawler.sitemaps.length > 0) {
        this.sitemap.value = providerCrawler.sitemaps[0].url;
        this.sitemap.type = providerCrawler.sitemaps[0].type;
        this.sitemap.status = providerCrawler.sitemaps[0].status;

        if (this.sitemap.status == "invalid") {
          this.sitemap.error = "Sitemap is not valid";
        }
      }
    },
    pollOnSitemapStatus() {
      this.sitemap.interval = setInterval(() => {
        this.getLogData();

        this.fetchProviderCrawler({
          id: this.providerCrawler.id
        }).then(
          providerCrawler => {
            this.loadSitemap(providerCrawler);

            if (this.sitemap.status === "verified") {
              this.getLogData();
              clearInterval(this.sitemap.interval);
              this.submitting = false;
              this.sitemap.validating = false;
            }

            if (this.sitemap.status === "invalid") {
              this.getLogData();
              this.sitemap.error = "Sitemap is not valid";
              clearInterval(this.sitemap.interval);
              this.submitting = false;
            }
          },
          error => {
            console.log(error);
            this.submitting = false;
          }
        );
      }, 1000);
    },
    resetSitemapCheck() {
      this.sitemap.validating = false;
      this.sitemap.error = null;
    },
    getLogData() {
      this.$logs
        .get(`/logs/${this.sitemap.id}.json`)
        .then(({ data }) => (this.sitemap.log = data))
        .catch(() => {});
    }
  }
};
</script>

<style lang="scss">
table {
  td {
    padding: 0.5em;
  }
}
</style>
