import jwt from "./jwt";
import domains from "./domains";

// Log in | Log out paths
const logInPath = "/developers/sign_in";
const logOutPath = "/user_accounts/sign_out";

export default {
  logInURL() {
    return domains["en"] + logInPath;
  },

  logOutURL() {
    return domains["en"] + logOutPath;
  },

  getApiAuthHeader() {
    return `Bearer ${jwt.get()}`;
  },

  getUserId() {
    try {
      return jwt.decode().sub;
    } catch (error) {
      return null;
    }
  },

  isLoggedIn() {
    try {
      if (!jwt.valid()) {
        this.logOut();
        return false;
      }
      return true;
    } catch (err) {
      return false;
    }
  },

  requireSignIn(redirPath = "/dashboard") {
    let url = `${this.logInURL()}?developers_dashboard_redir=${decodeURIComponent(
      redirPath
    )}`;
    location.replace(url);
  },

  logOut() {
    window.location.replace(this.logOutURL());
  }
};
