<template>
  <div class="el:amx-Pos(r)">
    <div class="el:amx-Pos(a)" style="width:100%;">
      <progress-bar v-if="loading || submitting" :indeterminate="true" />
    </div>
    <validation-observer v-slot="{ invalid, validate }" tag="div">
      <div class="container">
        <div class="row" v-if="!loading">
          <div class="col-12">
            <div class="el:amx-Pt(1em) el:amx-Pb(2em)">
              <validation-provider
                #default="{ errors }"
                tag="div"
                name="course_url"
                :rules="`required|url_path:${domain.domain}`"
              >
                <div
                  class="el:m-form-field el:amx-Mb(1em)"
                  :class="[
                    errors.length > 0 && 'el:m-form-field--error',
                    submitting && 'el:m-form-field--disabled'
                  ]"
                >
                  <div
                    class="el:m-form-field__label el:m-form-field__label--over"
                  >
                    <label for="course-url">
                      Course URL:
                    </label>
                  </div>
                  <div class="el:amx-D(f) el:amx-FxJc(sb)">
                    <div
                      class="el:m-form-field__input el:m-form-field__input--medium el:m-form-field__input--block el:amx-Mr(2em)"
                      style="flex: 1"
                    >
                      <input
                        name="course-url"
                        type="text"
                        :placeholder="
                          `e.g. https://${domain.domain}/path/to/course/page`
                        "
                        v-model.trim="courseUrl"
                      />
                      <icon
                        width="1.125em"
                        height="1.125em"
                        name="wrong"
                        class="el:m-form-field__input-icon el:amx-C_er"
                        v-if="errors.length > 0"
                      ></icon>
                      <icon
                        width="1.125em"
                        height="1.125em"
                        name="checked"
                        class="el:m-form-field__input-icon el:amx-C_pr"
                        v-if="courseUrl && errors.length == 0"
                      ></icon>
                    </div>
                    <button
                      type="submit"
                      class="btn btn--primary-flat btn--sm"
                      :disabled="submitting || invalid"
                      :value="submitting ? 'Submitting...' : 'Submit'"
                      @click.prevent="debugCourseUrl"
                    >
                      {{ submitting ? "Submitting..." : "Submit" }}
                    </button>
                  </div>
                  <div
                    v-if="errors.length > 0"
                    class="el:amx-Mt(0.25em) el:amx-C_er"
                  >
                    <span class="el:amx-Fs(0.75em)">{{ errors[0] }}</span>
                  </div>
                </div>
              </validation-provider>
            </div>
          </div>
        </div>
      </div>
      <div v-if="!loading">
        <div class="container">
          <div class="row">
            <div class="col-12">
              <h4 class="el:amx-Mb(0.5em)">
                System Log
              </h4>
              <log-output :log="previewCourseLog"></log-output>
            </div>
          </div>
        </div>
        <div
          v-if="previewCourse && !previewCourseError && previewCourseReady"
          class="el:amx-Bc_bg"
        >
          <div class="container">
            <div class="row">
              <div class="col-12">
                <h3
                  class="el:amx-Bob el:amx-Mt(1em) el:amx-Pb(0.25em) el:amx-Mb(1em)"
                >
                  Card
                </h3>

                <div class="el:amx-Mb(2em)">
                  <h5 class="el:amx-Mb(0.5em)">Desktop</h5>
                  <div class="row">
                    <div class="d-md-none">
                      <div
                        class="el:amx-Pt(1em) el:amx-Pb(1em) el:amx-Pr(1em) el:amx-Pl(1em)"
                      >
                        Desktop Card components are used when viewport is at
                        least 768px wide
                      </div>
                    </div>
                    <div class="d-sm-none d-md-block col-12 col-lg-9">
                      <course-card-desktop
                        class="el:amx-Bc_su el:amx-Pt(1em) el:amx-Pb(1em) el:amx-Pr(1em) el:amx-Pl(1em)"
                        :course="previewCourse.__indexed_json__"
                      ></course-card-desktop>
                    </div>
                  </div>
                </div>

                <div class="el:amx-Mb(3em)">
                  <h5 class="el:amx-Mb(0.5em)">Mobile</h5>
                  <course-card-mobile
                    class="el:amx-Bc_su el:amx-Pt(1em) el:amx-Pb(1em) el:amx-Pr(1em) el:amx-Pl(1em)"
                    :course="previewCourse.__indexed_json__"
                    style="min-width:300px;max-width:30%;"
                  >
                  </course-card-mobile>
                </div>

                <h3 class="el:amx-Bob el:amx-Pb(0.25em) el:amx-Mb(1em)">
                  Page
                </h3>

                <div class="el:amx-D(f) el:amx-FxJc(sb)">
                  <div
                    class="el:amx-Mb(2em) el:amx-D(ib)"
                    style="max-width:45%"
                  >
                    <div class="el:amx-D(f) el:amx-FxJc(sb)">
                      <h5 class="el:amx-Mb(0.5em)">Desktop</h5>
                      <a
                        :href="previewCourseUrl"
                        target="_blank"
                        class="el:amx-C_pr el:amx-Fw(b)"
                      >
                        <span class="el:amx-D(ib)">Open page</span>
                        <icon
                          width="0.75rem"
                          height="0.75rem"
                          :transform="'rotate(-90deg)'"
                          name="arrow-down"
                          class="el:amx-C_pr"
                        ></icon>
                      </a>
                    </div>
                    <a
                      class="overlayed-image"
                      :href="previewCourseUrl"
                      target="_blank"
                    >
                      <div class="overlayed-image__cta">
                        <a :href="previewCourseUrl" target="_blank"
                          >Open page in new tab</a
                        >
                      </div>
                      <img
                        :src="previewCourseDesktopScreenshotUrl"
                        style="max-width:100%"
                      />
                    </a>
                  </div>

                  <div
                    class="el:amx-Mb(3em) el:amx-D(ib)"
                    style="max-width:45%"
                  >
                    <h5 class="el:amx-Mb(0.5em)">Mobile</h5>
                    <img
                      :src="previewCourseMobileScreenshotUrl"
                      style="max-width:100%"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </validation-observer>
  </div>
</template>

<script>
import normalizeUrl from "normalize-url";
import CourseCardDesktop from "external/CourseCardDesktop.vue";
import CourseCardMobile from "external/CourseCardMobile.vue";
import Icon from "external/Icon.vue";
import ProgressBar from "external/ProgressBar.vue";
import LogOutput from "./LogOutput.vue";
import { createNamespacedHelpers } from "vuex";
import types from "../store/mutation-types";

const { mapActions: mapCrawlerDomainActions } = createNamespacedHelpers(
  "crawlerDomain"
);

const { mapActions: mapPreviewCourseActions } = createNamespacedHelpers(
  "previewCourse"
);

export default {
  data() {
    return {
      domain: null,
      providerCrawler: null,
      courseUrl: null,
      submitting: false,
      error: null,
      loading: false,
      previewCourse: null,
      previewCourseInterval: null,
      previewCourseError: null,
      previewCourseReady: false,
      previewCourseLog: []
    };
  },
  components: {
    CourseCardDesktop,
    CourseCardMobile,
    Icon,
    ProgressBar,
    LogOutput
  },
  created() {
    this.fetchData();
  },
  watch: {
    $route: "fetchData"
  },
  computed: {
    previewCourseUrl() {
      // eslint-disable-next-line no-undef
      return `${PREVIEW_COURSE_ENDPOINT}/${this.previewCourse.id}`;
    },
    previewCourseMobileScreenshotUrl() {
      let image;

      if (this.previewCourse.preview_course_images) {
        image = this.previewCourse.preview_course_images.filter(
          image => image.kind === "mobile"
        )[0];
      } else {
        return null;
      }

      if (image) {
        // eslint-disable-next-line no-undef
        return `${PREVIEW_COURSE_SCREENSHOT_ENDPOINT}/${image.file}`;
      } else {
        return null;
      }
    },
    previewCourseDesktopScreenshotUrl() {
      let image;

      if (this.previewCourse.preview_course_images) {
        image = this.previewCourse.preview_course_images.filter(
          image => image.kind === "desktop"
        )[0];
      } else {
        return null;
      }

      if (image) {
        // eslint-disable-next-line no-undef
        return `${PREVIEW_COURSE_SCREENSHOT_ENDPOINT}/${image.file}`;
      } else {
        return null;
      }
    }
  },
  methods: {
    ...mapCrawlerDomainActions({
      fetchCrawlerDomain: types.CRAWLER_DOMAIN_GET
    }),
    ...mapPreviewCourseActions({
      createPreviewCourse: types.PREVIEW_COURSE_CREATE,
      fetchPreviewCourse: types.PREVIEW_COURSE_GET
    }),
    fetchData() {
      this.error = this.domain = null;
      this.loading = true;

      this.fetchCrawlerDomain({
        domain: this.$route.params.domain
      }).then(
        domain => {
          this.loading = false;
          this.domain = domain;
          this.providerCrawler = domain.provider_crawler;
        },
        error => {
          if (error.response && error.response.data) {
            this.error = error.response.data.message;
          }
          console.log(error);
        }
      );
    },
    debugCourseUrl() {
      this.submitting = true;
      this.previewCourse = null;
      this.previewCourseError = null;
      this.previewCourseInterval = null;
      this.previewCourseReady = false;
      this.previewCourseLog = [];

      this.createPreviewCourse({
        providerCrawlerId: this.providerCrawler.id,
        id: this.$uuid(),
        courseUrl: normalizeUrl(this.courseUrl, {
          defaultProtocol: "https:"
        })
      }).then(
        previewCourse => {
          this.previewCourse = previewCourse;
          this.pollOnPreviewCourseStatus();
        },
        error => {
          console.log(error);
          this.submitting = false;
        }
      );
    },
    pollOnPreviewCourseStatus() {
      this.previewCourseInterval = setInterval(() => {
        this.getLogData();

        this.fetchPreviewCourse({
          id: this.previewCourse.id
        }).then(
          previewCourse => {
            if (previewCourse.status === "succeeded") {
              this.getLogData();
              this.previewCourse = previewCourse;
              clearInterval(this.previewCourseInterval);
              this.submitting = false;
              this.previewCourseReady = true;
            }

            if (previewCourse.status === "failed") {
              this.getLogData();
              this.previewCourseError = "failed";
              clearInterval(this.previewCourseInterval);
              this.submitting = false;
            }
          },
          error => {
            console.log(error);
            this.submitting = false;
          }
        );
      }, 1000);
    },
    getLogData() {
      this.$logs
        .get(`/logs/${this.previewCourse.id}.json`)
        .then(({ data }) => (this.previewCourseLog = data))
        .catch(() => {});
    }
  }
};
</script>

