import { pgrestApi as api } from "../../config/axiosInstances";
import types from "../mutation-types";

const state = {
  id: null,
  status: "initial",
  name: null,
  description: null,
  provider_crawler_id: null,
  provider_id: null,
  preview_course_images: [],
  __indexed_json__: {}
};

const mutations = {
  [types.PREVIEW_COURSE_GET](state, payload) {
    for (const key in payload) {
      if (payload.hasOwnProperty(key)) {
        state[key] = payload[key];
      }
    }
  }
};

const actions = {
  [types.PREVIEW_COURSE_CREATE](
    { commit },
    { id, providerCrawlerId: provider_crawler_id, courseUrl: url }
  ) {
    return new Promise((resolve, reject) => {
      api
        .post(
          "/preview_courses",
          { id, provider_crawler_id, url },
          {
            headers: { Prefer: "return=representation" }
          }
        )
        .then(
          response => {
            const payload = response.data[0];
            commit(types.PREVIEW_COURSE_GET, payload);
            resolve(payload);
          },
          error => {
            reject(error);
          }
        );
    });
  },
  [types.PREVIEW_COURSE_GET]({ commit }, { id }) {
    return new Promise((resolve, reject) => {
      api
        .get(`/preview_courses?id=eq.${id}&select=*,preview_course_images(*)`, {
          headers: { Prefer: "return=representation" }
        })
        .then(
          response => {
            const payload = response.data[0];
            commit(types.PREVIEW_COURSE_GET, payload);
            resolve(payload);
          },
          error => {
            reject(error);
          }
        );
    });
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};
