import { pgrestApi as api } from "../../config/axiosInstances";
import types from "../mutation-types";

const state = {
  id: null
};

const mutations = {
  [types.CRAWLER_DOMAIN_GET](state, payload) {
    for (const key in payload) {
      if (payload.hasOwnProperty(key)) {
        state[key] = payload[key];
      }
    }
  }
};

const actions = {
  [types.CRAWLER_DOMAIN_LIST]() {
    return new Promise((resolve, reject) => {
      api
        .get(
          "/crawler_domains?authority_confirmation_status=eq.confirmed&select=*,provider_crawler:provider_crawlers(*)",
          {
            headers: { Prefer: "return=representation" }
          }
        )
        .then(
          response => {
            resolve(response.data);
          },
          error => {
            reject(error);
          }
        );
    });
  },
  [types.CRAWLER_DOMAIN_GET]({ commit }, { domain, id }) {
    return new Promise((resolve, reject) => {
      api
        .get(
          `/crawler_domains?domain=eq.${domain}&select=*,provider_crawler:provider_crawlers(*)`,
          {
            headers: { Prefer: "return=representation" }
          }
        )
        .then(
          response => {
            resolve(response.data[0]);
          },
          error => {
            reject(error);
          }
        );
    });
  },
  [types.CRAWLER_DOMAIN_VERIFY]({ commit }, { id }) {
    return new Promise((resolve, reject) => {
      api
        .patch(
          `/crawler_domains?id=eq.${id}`,
          {
            authority_confirmation_status: "confirming"
          },
          {
            headers: {
              Prefer: "return=representation",
              Action: "DomainAuthorityVerification"
            }
          }
        )
        .then(
          response => {
            resolve(response);
          },
          error => {
            reject(error);
          }
        );
    });
  },
  [types.CRAWLER_DOMAIN_CREATE]({ commit }, { domain }) {
    return new Promise((resolve, reject) => {
      api
        .post(
          "/crawler_domains",
          {
            domain: domain
          },
          {
            headers: { Prefer: "return=representation" }
          }
        )
        .then(
          response => {
            resolve(response.data[0]);
          },
          error => {
            reject(error);
          }
        );
    });
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};
