import { pgrestApi as api } from "../../config/axiosInstances";
import types from "../mutation-types";
import normalizeUrl from "normalize-url";

const state = {
  name: null,
  firstName: null,
  avatar_url: null,
  username: null,
  interests: [],
  loaded: false
};

const mutations = {
  [types.PROFILE_GET](state, payload) {
    state.name = payload.name || state.name;
    state.username = payload.username || state.username;
    state.interests = payload.interests || state.interests;
    state.avatar_url = normalizeUrl(payload.avatar_url || state.avatar_url, {
      defaultProtocol: "https:"
    });
    if (state.name) {
      state.firstName = state.name.split(/\s+/)[0];
    }
    state.loaded = true;
  }
};

const actions = {
  [types.PROFILE_GET]({ commit }, { user_account_id }) {
    return new Promise((resolve, reject) => {
      api.get(`/profiles?user_account_id=eq.${user_account_id}`).then(
        response => {
          commit(types.PROFILE_GET, response.data[0]);
          resolve(response);
        },
        error => {
          reject(error);
        }
      );
    });
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};
