import types from "../mutation-types";

const state = {
  domains: [],
  currentDomain: null,
  loaded: false
};

const mutations = {
  [types.SHARED_CHANGE](state, payload) {
    for (const key in payload) {
      if (payload.hasOwnProperty(key)) {
        state[key] = payload[key];
      }
    }
  }
};

export default {
  namespaced: true,
  state,
  mutations
};
