<template>
  <div class="el:amx-Pos(r)">
    <div class="el:amx-Pos(a)" style="width:100%;">
      <progress-bar v-if="addingDomain && !verified && countdownTimer.status !== 'stopped'" :indeterminate="true" />
    </div>
    <div class="el:amx-Pt(3em)" style="max-width:992px;margin:0 auto">
      <div
        class="el:amx-Fs(0.875em) el:amx-Mb(2.5em) el:amx-Mr(auto) el:amx-Ml(auto)"
        style="width:75%"
      >
        <div class="el:m-wizard-progress">
          <span class="el:m-wizard-progress__line"></span>
          <ol>
            <li :data-state="wizardStep(1)" data-label="Enter site domain"></li>
            <li
              :data-state="wizardStep(2)"
              data-label="Ownership verification"
            ></li>
            <li :data-state="wizardStep(3)" data-label="Verify ownership"></li>
          </ol>
        </div>
      </div>

      <validation-observer v-slot="{ invalid, validate }" slim>
        <div
          class="el:amx-Bc_su el:amx-Mb(1.5em) el:amx-Bo"
          style="padding:4em;"
        >
          <div v-if="step == 1">
            <h2 class="el:amx-Fs(1.375em) el:amx-Mb(1em)">Enter site domain</h2>
            <validation-provider
              #default="{ errors }"
              slim
              name="domain"
              rules="required|domain"
            >
              <div
                class="el:m-form-field"
                style="max-width:75%;"
                :class="[
                  (errors.length > 0 || domainUniqueness.error) &&
                    'el:m-form-field--error'
                ]"
              >
                <div
                  class="el:amx-Mt(2em) el:m-form-field__input el:m-form-field__input--medium el:m-form-field__input--block"
                >
                  <input
                    v-model.trim="domain"
                    name="field"
                    type="text"
                    placeholder="e.g: mydomain.com"
                    @input="resetDomainCheck"
                  />
                  <svg
                    v-if="errors.length > 0 || domainUniqueness.error"
                    class="el:m-form-field__input-icon el:amx-C_er"
                    width="1.125em"
                    height="1.125em"
                    viewbox="0 0 24 24"
                  >
                    <use xlink:href="#icons-wrong" />
                  </svg>
                  <svg
                    v-if="
                      domain != null &&
                        errors.length == 0 &&
                        domainUniqueness.error == null
                    "
                    class="el:m-form-field__input-icon el:amx-C_pr"
                    width="1.125em"
                    height="1.125em"
                    viewbox="0 0 24 24"
                  >
                    <use xlink:href="#icons-checked" />
                  </svg>
                  <div
                    v-if="errors.length > 0 || domainUniqueness.error"
                    class="el:amx-Mt(0.25em) el:amx-C_er"
                  >
                    <span class="el:amx-Fs(0.75em)">{{
                      errors.length > 0 ? errors[0] : domainUniqueness.error
                    }}</span>
                  </div>
                </div>
              </div>
            </validation-provider>
          </div>

          <div v-if="step == 2">
            <h2 class="el:amx-Fs(1.375em) el:amx-Mb(1em)">
              Select a method to verify {{ domain }} ownership
            </h2>
            <tabs :size="3" current-tab="tab1">
              <template #tab1>
                HTML Method
              </template>

              <template #tab2>
                TXT Method
              </template>

              <template #tab3>
                CNAME Method
              </template>

              <template #tab1-content>
                <p class="el:amx-Fs(0.875em) el:amx-Mt(1.5em)">
                  Place the code below inside your
                  <pre class='el:amx-C_prV' style='display:inline;'><code>&lt;head&gt;</code></pre>
                  tag
                </p>

                <div
                  class="html-code"
                  style="font-size:0.75em;text-align:left;position:relative;border:1px solid #eee"
                >
                  <a
                    v-tooltip="{
                      content: 'copied',
                      offset: '2px',
                      trigger: 'manual',
                      show: hasCopied,
                      classes: ['el:amx-Fs(0.625em)']
                    }"
                    v-clipboard:copy="htmlVerificationCode"
                    v-clipboard:success="onCopy"
                    style="position:absolute;top:-0.375em;right:-0.375em;background-color:#fff;line-height:0.75em"
                  >
                    <svg width="1.5em" height="1.5em">
                      <use xlink:href="#icons-clipboard" />
                    </svg>
                  </a>
                  <highlight-code lang="html">
                    &lt;!-- Classpert Site Verification --&gt; &lt;meta
                    name='classpert-site-verification' content={{
                      domainVerificationToken
                    }}
                    /&gt;
                  </highlight-code>
                </div>
              </template>

              <template #tab2-content>
                <p class="el:amx-Fs(0.875em) el:amx-Mt(1.5em)">
                  Create a TXT for your domain <b>{{ domain }}</b> as below
                </p>

                <table
                  class="dns-table el:amx-Fs(0.75em)"
                  cellpadding="0"
                  cellspacing="0"
                >
                  <thead>
                    <th>Record Type</th>
                    <th>Value</th>
                  </thead>
                  <tbody>
                    <tr>
                      <td>TXT</td>
                      <td class="el:amx-Va(m)">
                        classpert-site-verification={{
                          domainVerificationToken
                        }}
                        <a
                          v-tooltip="{
                            content: 'copied',
                            offset: '2px',
                            trigger: 'manual',
                            show: hasCopied,
                            classes: ['el:amx-Fs(0.625em)']
                          }"
                          v-clipboard:copy="domainVerificationToken"
                          v-clipboard:success="onCopy"
                        >
                          <svg
                            class="el:amx-Va(m)"
                            width="1.25em"
                            height="1.25em"
                          >
                            <use xlink:href="#icons-clipboard" />
                          </svg>
                        </a>
                      </td>
                    </tr>
                  </tbody>
                </table>

                <div
                  class="el:amx-Fs(0.625em) el:amx-P(0.75em) el:amx-Bc_bg el:amx-Mt(3em)"
                >
                  Sometimes DNS records take a while to make their way across
                  the Internet. If we don't find the record immediately, we'll
                  check for it periodically and when we find the record we'll
                  make you a verified owner. To maintain your verification
                  status don’t remove the record, even after verification
                  succeeds.
                </div>
              </template>

              <template #tab3-content>
                <p class="el:amx-Fs(0.875em) el:amx-Mt(1.5em)">
                  Create a CNAME for your domain <b> {{ domain }}</b> as below
                </p>

                <table
                  class="dns-table el:amx-Fs(0.75em)"
                  cellpadding="0"
                  cellspacing="0"
                >
                  <thead>
                    <th>Record Type</th>
                    <th>Name</th>
                    <th>Target / Value / Points To</th>
                  </thead>
                  <tbody>
                    <tr>
                      <td>CNAME</td>
                      <td>{{ domainVerificationToken }}.{{ domain }}</td>
                      <td class="el:amx-Va(m)">
                        verification.classpert.com
                        <a
                          v-tooltip="{
                            content: 'copied',
                            offset: '2px',
                            trigger: 'manual',
                            show: hasCopied,
                            classes: ['el:amx-Fs(0.625em)']
                          }"
                          v-clipboard:copy="domainVerificationToken"
                          v-clipboard:success="onCopy"
                        >
                          <svg
                            class="el:amx-Va(m)"
                            width="1.25em"
                            height="1.25em"
                          >
                            <use xlink:href="#icons-clipboard" />
                          </svg>
                        </a>
                      </td>
                    </tr>
                  </tbody>
                </table>

                <div
                  class="el:amx-Fs(0.625em) el:amx-P(0.75em) el:amx-Bc_bg el:amx-Mt(3em)"
                >
                  Sometimes DNS records take a while to make their way across
                  the Internet. If we don't find the record immediately, we'll
                  check for it periodically and when we find the record we'll
                  make you a verified owner. To maintain your verification
                  status don’t remove the record, even after verification
                  succeeds.
                </div>
              </template>
            </tabs>
          </div>

          <div v-if="step == 3">
            <div>
              <h2 class="el:amx-Fs(1.375em) el:amx-Mb(1em) el:amx-Ta(c)">
                <span v-if="verified">
                  Domain successfully verified! <br /><span
                    style="font-weight:300"
                  >
                    Redirecting you to configuration dashboard ...</span
                  >
                </span>
                <span v-else>
                  <div v-if="countdownTimer.status === 'started'">
                    Verifying domain ownership <br /><span
                    style="font-weight:300"
                  >
                    This process can take a while, please wait ...</span
                  ><br/>
                    <span class='el:amx-Fs(0.5em)'>
                      Countdown until expiration: {{ countdownTimerRemaining }}s
                    </span>
                  </div>
                  <div style="font-weight:300" v-if="countdownTimer.status === 'stopped'">
                    Domain verification expired, try again later
                  </div>
                </span>
              </h2>
              <log-output :log="log"></log-output>
            </div>
          </div>
        </div>

        <button
          v-if="step > 1 && !addingDomain && !verified"
          @click="goBack"
          class="btn btn--primary-border btn--md"
        >
          Back
        </button>
        <button
          v-if="step < 3"
          @click="
            step === 1 ? checkDomain().then(goForward, () => {}) : goForward()
          "
          class="el:amx-F(r) btn btn--primary-flat btn--md"
          :disabled="
            step == 1 &&
              (invalid ||
                domainUniqueness.validating ||
                (!domainUniqueness.valid && domainUniqueness.error))
          "
        >
          {{
            step === 1
              ? domainUniqueness.validating
                ? "Validating"
                : "Validate"
              : "Next"
          }}
        </button>
      </validation-observer>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
import md5 from "md5";
import types from "../store/mutation-types";
import session from "../config/session";
import LogOutput from "./LogOutput.vue";
import ProgressBar from "external/ProgressBar.vue";
import CountdownTimer from "../mixins/countdown-timer";

const { mapActions: mapCrawlerDomainActions } = createNamespacedHelpers(
  "crawlerDomain"
);
const { mapActions: mapProviderCrawlerActions } = createNamespacedHelpers(
  "providerCrawler"
);

const { mapMutations: mapSharedMutations } = createNamespacedHelpers("shared");

export default {
  mixins: [ CountdownTimer ],
  data() {
    return {
      step: 1,
      domain: null,
      crawlerDomain: null,
      log: [],
      addingDomain: false,
      verified: false,
      addingDomainInterval: null,
      domainUniqueness: {
        validating: false,
        valid: false,
        checking: false,
        error: null
      },
      hasCopied: false
    };
  },
  components: {
    LogOutput,
    ProgressBar
  },
  computed: {
    domainVerificationToken() {
      if (this.domain) {
        // eslint-disable-next-line no-undef
        return md5(`${this.domain}${DOMAIN_VERIFICATION_SALT}`);
      } else {
        return null;
      }
    },
    htmlVerificationCode() {
      return `
            <!-- Classpert Site Verification -->
            <meta name='classpert-site-verification' content='${this.domainVerificationToken}' />
          `;
    }
  },

  methods: {
    ...mapProviderCrawlerActions({
      listProviderCrawlers: types.PROVIDER_CRAWLER_LIST,
      createProviderCrawler: types.PROVIDER_CRAWLER_CREATE
    }),
    ...mapCrawlerDomainActions({
      fetchCrawlerDomain: types.CRAWLER_DOMAIN_GET,
      createCrawlerDomain: types.CRAWLER_DOMAIN_CREATE
    }),
    ...mapSharedMutations({
      changeShared: types.SHARED_CHANGE
    }),
    resetDomainCheck() {
      this.domainUniqueness.valid = false;
      this.domainUniqueness.validating = false;
      this.domainUniqueness.error = null;
    },
    checkDomain() {
      this.domainUniqueness.validating = true;

      return new Promise((resolve, reject) => {
        return this.fetchCrawlerDomain({ domain: this.domain }).then(
          domain => {
            if (domain) {
              if (
                domain.authority_confirmation_status === "confirmed" ||
                (domain.provider_crawler &&
                  domain.provider_crawler.status !== "deleted" &&
                  domain.provider_crawler.user_account_ids.length > 0 &&
                  domain.provider_crawler.user_account_ids.indexOf(
                    parseInt(session.getUserId())
                  ) === -1)
              ) {
                this.domainUniqueness.valid = false;
                this.domainUniqueness.validating = false;
                this.domainUniqueness.error = "Domain already taken";
                reject(true);
              } else {
                // domain is unconfirmed or deleted, good to go
                this.domainUniqueness.valid = true;
                this.domainUniqueness.validating = false;
                this.domainUniqueness.error = null;
                resolve(true);
              }
            } else {
              // domain doesn't exist
              this.domainUniqueness.valid = true;
              this.domainUniqueness.validating = false;
              this.domainUniqueness.error = null;
              resolve(true);
            }
          },
          () => {
            // generic error while trying to check uniqueness
            this.domainUniqueness.valid = false;
            this.domainUniqueness.validating = false;
            this.domainUniqueness.error = "Error while checking domain";
            reject(true);
          }
        );
      });
    },
    onCopy() {
      this.hasCopied = true;
      setTimeout(() => (this.hasCopied = false), 1500);
    },

    goBack() {
      this.step -= 1;
    },

    goForward() {
      this.step += 1;

      if (this.step == 3) {
        this.addDomain();
      }
    },

    wizardStep(step) {
      if (this.step == step) {
        return "active";
      } else if (this.step > step) {
        return "done";
      } else {
        return "inactive";
      }
    },

    addDomain() {
      this.addingDomain = true;
      this.countdownTimerStart();

      this.createCrawlerDomain({
        domain: this.domain
      }).then(
        crawlerDomain => {
          this.crawlerDomain = crawlerDomain;
          this.addingDomainInterval = setInterval(() => {
            this.getLogData();
            this.fetchCrawlerDomain({ domain: crawlerDomain.domain }).then(
              ({ authority_confirmation_status, provider_crawler }) => {
                if (
                  provider_crawler &&
                  authority_confirmation_status === "confirmed"
                ) {
                  this.verified = true;
                  this.domain = null;
                  this.addingDomain = false;
                  clearInterval(this.addingDomainInterval);
                  this.changeShared({
                    currentDomain: this.crawlerDomain.domain,
                    domains: [
                      ...this.$store.state.shared.domains,
                      this.crawlerDomain.domain
                    ]
                  });
                  setTimeout(() => {
                    this.$router.push({
                      name: "domain-edit",
                      params: { domain: this.crawlerDomain.domain }
                    });
                  }, 2000);
                }

                if( authority_confirmation_status === "failed" ) {
                  this.countdownTimerStop();
                }
              },
              () => {
                this.addingDomain = false;
              }
            );
          }, 2000);
        },
        // eslint-disable-next-line no-unused-vars
        error => {
          this.addingDomain = false;
        }
      );
    },
    getLogData() {
      this.$logs
        .get("/logs/" + this.crawlerDomain.id + ".json")
        .then(({ data }) => {
          if (data && data.length && this.log != data) {
            this.log = data;
            this.countdownTimerReset();
          }
        })
        .catch(() => {});
    }
  }
};
</script>

